<template>
                        <swiper style="border: 1px solid red; width: 400px; height: 400px;" :slides-per-view="2"
                        @swiper="onSwiper">
                        <swiper-slide>하바나우냐냐</swiper-slide>
                        <swiper-slide>Slide 2</swiper-slide>
                        <swiper-slide>Slide 3</swiper-slide>
                        <swiper-slide>Slide 4</swiper-slide>
                    </swiper>
</template>

<script>
    import {
        Swiper,
        SwiperSlide
    } from 'swiper/vue';
    import 'swiper/swiper-bundle.css';
export default {
            components: {
            Swiper,
            SwiperSlide
        },
                methods: {
            onSwiper(swiper) {
                this.swiper = swiper;
            },

            handleSlideTo() {
                this.swiper.slideTo(3);
            },
        },
}
</script>

